// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },

  template: '<App/>'
})

new Vue({
  el: '#pen',
  components: {
      hl: highlighter(),
  },
});


function highlighter() {
  const startLineOffset = -100;
  class Style {
      constructor(highlighter) {
          const color = highlighter.color
          this['background'] = `linear-gradient(${color}, ${color})`;
          this['background-size']      = '0% 100%';
          this['background-position']  = '0% 100%';
          this['background-repeat']    = 'no-repeat';
          this['transition-delay']     = '1s';
          this['transition-duration']  = '4.5s';
      }
  };
  
   return {
      template: 
          '<span v-bind:style="style">' +
              '&nbsp;<slot></slot>&nbsp;' +
          '</span>',
      props: ['color'],
      data: function() {
          return {
              style: new Style(this),
          };
      },
      mounted: function() {
          window.addEventListener('scroll', this.scroll);
      },
      methods: {
          scroll: function() {
              if (this.startLine() < this.domRectTop()){
                  this.style['background-size'] = '  0% 100%';
              } else {
                  this.style['background-size'] = '100% 100%';
              }
          },
          startLine: function() {
              return window.innerHeight + startLineOffset;
          },
          domRectTop: function() {
              return this.$el.getBoundingClientRect().top;
          },
      },
  };
};


