import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import News from '@/components/News'
import Company from '@/components/Company'
import Business from '@/components/Business'
import Career from '@/components/Career'
import Contact from '@/components/Contact'
import Access from '@/components/Access'
import css from '../assets/newsun.css'
import mcss from '../assets/media.css'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/news',
      name: 'News',
      component: News
    },
    {
      path: '/company',
      name: 'Company',
      component: Company
    },
    {
      path: '/business',
      name: 'Business',
      component: Business
    },
    {
      path: '/career',
      name: 'Career',
      component: Career
    },
   
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/access',
      name: 'Access',
      component: Access
    }
  ]
})



